#header {
  width: 100%;
  margin-top: 10em;
}

#header h1 {
  margin: 0 0 10px 0;
  font-size: 42px;
  font-weight: 900;
  color: #0d1f35;
}



#header .band-name {
  color: #ff5203;
}

#header h2 {
  color: #0d1f35;
  font-size: 19px;
}

#header .btn-get-started {
  font-weight: 500;
  font-size: 16px;
  letter-spacing: 1px;
  display:inline-flexbox;
  padding: 10px 30px;
  padding-top: 8px;
  border-radius: 50px;
  transition: 0.5s;
  background: #ff5203;
  color: #fff;
  border-radius: 2px solid #ff5203;
  text-decoration: none;
  border: 2px solid #ff5203;
}

#header .btn-get-started:hover {
  background: #fff;
  color: #ff5203;

  border: 2px solid #ff5203;
}

.header-img {
  text-align: center;
}

/* best packages */


.button {
  transition: all .2s ease-in-out;
  background: #fff !important;
  color: #000 !important;
  font-weight: 900;
  box-shadow: 0 2px 3px 0 rgba(0,0,0,0.2), 0 2px 6px 0 rgba(0,0,0,0.19);
  letter-spacing: 1px;
  display:inline-flexbox;
  padding: 10px 30px;
  border-radius: 50px;
}

.button2 {
  font-weight: 500;
  font-size: 16px;
  letter-spacing: 1px;
  display:inline-flexbox;
  padding: 10px 30px;
  border-radius: 50px;
  transition: 0.5s;
  background: #ff5203;
  color: #fff;
  border-radius: 2px solid #ff5203;
  text-decoration: none;
  border: 2px solid #ff5203;
}


.button:hover {
  transition: all .2s ease-in-out;
 
  background: #ff5203 !important;
  color: #fff !important;
  font-weight: 900;
  
}

.button2:hover {
  transition: all .2s ease-in-out;
 
  background: #fff !important;
  color: #ff5203 !important;
  font-weight: 900;
  
}

.button:active {

  background: #ff5203 !important;
  color: #fff !important;
  font-weight: 900;
  
  
}

