/* our client */

.btns {
  background-color:#ff5203 !important;
  position: absolute !important;
  bottom: 34px !important;
  right: 50% !important;
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg);
}

.btns button {
  margin: 0 0.8em;
  font-size: 35px;
  
}

.btns button p {
  font-size: 30px;
  font-weight: bold;
